
.avatar--tags {
  width: 80px;
  height: 80px;
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}
.scroll {
  max-height: 400px;
  overflow: scroll;
  position: relative;
}
.shadow {
  position: absolute;
  z-index: 2;
  pointer-events: none;
  right: 0;
  bottom: 0;
  width: 100%;
  height: 24px;
  background: linear-gradient(to bottom, rgba(245,245,245,0) 0%,rgba(245,245,245,1) 100%);
  filter: progid:DXImageTransform.Microsoft.gradient( startColorstr='#00f5f5f5', endColorstr='#f5f5f5',GradientType=0 );
}
.developer {
  background: #1094F6;
  color: #fff;
}
.designer {
  background: #12D469;
  color: #fff;
}
.youtuber {
  background: #F72848;
  color: #fff;
}
.clevel {
  background: #973CBD;
  color: #fff;
}
.junior {
  background: #FFCC00;
  color: #222;
}



