
.svg-turkiye-haritasi {
  max-width: 1140px;
  margin: 0 auto;
  text-align: center;
}
.svg-turkiye-haritasi svg {
  width: 100%;
  height: auto;
}
.il-isimleri {
  position: absolute;
  z-index: 2;
}
.il-isimleri div {
  display: inline-block;
  background: #1094F6;
  color: #fff;
  padding: 8px 16px;
  border-radius: 4px;
}
#svg-turkiye-haritasi path {
  cursor: pointer;
  fill: #222;
}
#svg-turkiye-haritasi path:hover {
  fill: #1094F6;
}



